import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Settings from 'containers/settings/Settings';
import Help from 'containers/Help';
import WidgetSettings from 'containers/WidgetSettings';
import Header from 'components/Header';
import Layuot from 'components/layout/Layout'; 
import Navigation from 'components/Navigation';
import WidgetsConfiguration from 'containers/WidgetConfiguration';

import {commonRoutes} from 'utils/routes';

const Application = ({ appId, groupId }) => (
    <Switch>
        <>
            <Header />
            <Layuot leftBar={<Navigation routes={commonRoutes} />}>
                <Route path="/settings">
                    <Settings appId={appId} groupId={groupId} />
                </Route>
                <Route path="/help" component={Help} />
                <Route path="/widget" component={WidgetSettings} />
                <Route path="/configuration/:type" component={WidgetsConfiguration} />
            </Layuot>
        </>
    </Switch>
);

export default Application;