import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import connect from '@vkontakte/vk-connect';

import Landing from 'containers/Landing';
import Application from 'containers/Application';

import {getUrlParams} from './utils/urlParamsHelper';
import { urlParams } from './consts/urlParams';

import './App.scss';

const App = () => {
	const [groupId, setGroupId] = useState(null);
	const [appId, setAppId] = useState(null);

	const history = useHistory();

	useEffect(() => {
		connect.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppAddToCommunityResult') {
				console.info('connect');
			}
		});

		setGroupId(getUrlParams(urlParams.vkGroupId));
		setAppId(getUrlParams(urlParams.vkAppId));

		function handleRedirect() {
			if(groupId) {
				history.push('/settings');
			}
		}

		handleRedirect();
	}, [history, groupId])

	async function connectToGroup() {
		await connect.sendPromise('VKWebAppAddToCommunity');
	}

	console.log('groupID', groupId);

	return !groupId ? 
		<Landing handleConnect={connectToGroup} /> : 
		<Application groupId={groupId} appId={appId} />
}

export default App;

