import React from 'react';
import { Route, Switch } from 'react-router-dom';

import RenderComponent from 'components/RenderComponent';
import WidgetLayout from 'components/layout/WidgetLayout';
import TilesLayout from 'components/layout/TilesLayout';

import {widgetRoutes} from 'utils/routes';


const WidgetConfiguration = () => {
    const somedata = 'Какие-то данные!';
    return (
        <Switch>
            {widgetRoutes.map(route => (
                <Route path={route.path}>
                    <WidgetLayout>
                        <TilesLayout flex={3}>
                            <RenderComponent component={route.component} data={somedata} />
                        </TilesLayout>
                        <TilesLayout flex={2}>
                            <div>settings frorm</div>
                        </TilesLayout>
                    </WidgetLayout>
                </Route>
            ))}
        </Switch>
    );
};

export default WidgetConfiguration;